import {Base} from "@/utils/base.js"
class User extends Base{
	constructor() {
		super();
	}
	//
	isBind(param,callback) {
		var params = {
		  url:'member/auth/is-binding',
		  data:param,
		  method:'post',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//
	index(param,callback) {
		var params = {
		  url:'member/member/index',
		  data:param,
		  method:'get',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//vip
	vipList(param,callback) {
		var params = {
		  url:'member/member/vip-list',
		  data:param,
		  method:'get',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//vip
	vipMin(param,callback) {
		var params = {
		  url:'member/member/vip-min',
		  data:param,
		  method:'get',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//
	footPrint(param,callback) {
		var params = {
		  url:'member/footprint/index',
		  data:param,
		  method:'get',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//
	collect(param,callback) {
		var params = {
		  url:'member/collect/index',
		  data:param,
		  method:'get',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//
	distributeLog(param,callback) {
		var params = {
		  url:'member/distribute/index',
		  data:param,
		  method:'get',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
};

export { User };