import {Base} from "@/utils/base.js"
class Public extends Base{
	constructor() {
		super();
	}
	//
	login(param,callback) {
		var params = {
		  url:param.url,
		  data:param,
		  method:'post',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//
	xcxAuth(param,callback){
		var params = {
		  url:'third-party/wechat-mp',
		  data:param,
		  method:'post',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//
	register(param,callback) {
		var params = {
		  url:'site/register',
		  data:param,
		  method:'post',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//
	config(param,callback) {
		var params = {
		  url:'site/config',
		  data:param,
		  method:'post',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
	//
	smsSend(param,callback){
		var params = {
		  url:'site/sms-code',
		  data:param,
		  method:'post',
		  sCallback:function(data){
			callback && callback(data);
		  }
		}
		this.request(params);
	}
};

export { Public };